@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: white;
  background-color: var(--primaryBackgroundColor);
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  font-weight: bold;
  color: black;
  color: var(--primaryTextColor);
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

html {
  /* our light-mode / default theme variable values */
  --primaryTextColor: black;
  --primaryBackgroundColor: white;

}

html.dark {
  /* here's where we change the variable values for dark mode */
  --primaryTextColor: white;
  --primaryBackgroundColor: #282c34;
    
}


.type {
  border-right: solid 3px rgba(0,255,0,.75);
  white-space: nowrap;
  overflow: hidden;    
  font-family: 'Source Code Pro', monospace;  
  font-size: 28px;
  color: black;
  color: var(--primaryTextColor);
  margin: 0 auto;
}

/* Animation */
.type {
  -webkit-animation: animated-text 4s steps(29,end) 1s 1 normal both,
             animated-cursor 600ms steps(29,end) infinite;
          animation: animated-text 4s steps(29,end) 1s 1 normal both,
             animated-cursor 600ms steps(29,end) infinite;
}

/* text animation */

@-webkit-keyframes animated-text{
  from{width: 0;}
  to{width: 352px;}
}

@keyframes animated-text{
  from{width: 0;}
  to{width: 352px;}
}

/* cursor animations */

@-webkit-keyframes animated-cursor{
  from{border-right-color: black;border-right-color: var(--primaryTextColor);}
  to{border-right-color: transparent;}
}

@keyframes animated-cursor{
  from{border-right-color: black;border-right-color: var(--primaryTextColor);}
  to{border-right-color: transparent;}
}


.button {
  margin-top: 25px;
  margin-right: 25px;
  position:absolute;
  top:0;
  right:0;
}


